import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            items: [],
            act: 1001011,
            children: [],
            province: null,
			isHot:0,
			AreaName:'',
        }
    },
    created(){
        this.pull(0,'')
    },
	 mounted(){
		  this.$bridge.callhandler('pullArea','',(data) => {
		 	if(data){
		 		this.AreaName = data
		 	}else{
		 		this.AreaName = ''
		 	}
		     responseCallback('接收到方法')
		 })
// 		 if(this.$store.state.inspctArea){
// 			 this.AreaName = this.$store.state.inspctArea.AreaName
// 		 }
	},
    methods:{
		AreaNameClick(){
			this.$store.dispatch('pushinspctArea',{
			    AreaName: this.AreaName,
			    AreaId: 0
			})
			this.$router.back()
		},
        choose(i,idx){
			this.act = idx
			if(idx === 1008611){
				this.pullHot()
			}
			else if(idx === 1001011){
			    this.$store.dispatch('pushinspctArea',{
			        AreaName: '',
			        AreaId: -1
			    })
			    this.$router.back()
			}
			else{
			   this.$post('PlatFormAPI/Area/QueryMallGoodsAreaList',{
			    AreaId: i.AreaId,
			   	AreaName:i.AreaName,
			   	//IsHot:-1
			   }).then(o => {
			       this.children = o
			   })
			}
        },
		 pullHot(){
		    this.$get('PlatFormAPI/Area/QueryHostArea').then(o => {
		        this.children = o
		    })
		},
        pull(a,b){
			this.$post('PlatFormAPI/Area/QueryMallGoodsAreaList',{
			    AreaId: 0,
				AreaName:'',
				//IsHot:-1
			}).then(o => {
			    this.items = o
			})
        },
		toAll(){
			if(this.act == 1001011){
				this.province = {
					AreaName: '',
		            AreaId: 0
				}
			}
		    this.$store.dispatch('pushinspctArea',this.province)
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId',0)
		    this.$router.back()
		},
        goList(i){
            this.$store.dispatch('pushinspctArea',i)
			this.$store.dispatch('pushgoodList', null)
			this.$store.dispatch('pushpreId',0)
            this.$router.back()
        }
    }
}



